footer
    background: var(--primary-blue-train)
.contact-us

    div
        text-align: center
        padding-top: 60px
        border-style: solid
        border-color: gray
        border-radius: 10px
        border-width: 1px
        background: #fff
        position: relative
        top: -100px

        @media screen and (min-width: 576px)
            max-width: 464px
            margin: auto


        
        h2
            margin: 0
            padding-bottom: 20px
            font-size: 36px
            @media screen and (min-width: 576px)
                font-size: 64px
            
            
        h4
            margin: 0
            padding: 4px 16px
            font-size: 20px
            line-height: 140%
            font-weight: normal
            @media screen and (min-width: 576px)
                font-size: 28px

        .street
            padding-top: 30px
            padding-bottom: 60px
            @media screen and (min-width: 576px)
                font-size: 18px


    
       

   